.c-grid-wrapper {
  overflow: hidden;
  position: relative;
  color: #333;
}
.c-grid-header {
  position: relative;
  background-color: #eaeaea;
  border-top: 1px solid #c6c6c6;
  border-left: 1px solid #c6c6c6;
  border-right: 1px solid #c6c6c6;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.35);
  outline: 0;
  font-weight: bold;
  overflow: hidden;
}
.c-grid-header ul.c-header-row {
  position: relative;
  /*left: -1000px;*/
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
  white-space: nowrap;
}
.c-grid-header ul.c-header-row > li.c-header-cell {
  /*position: relative;*/
  float: left;
  /*left: 1000px;*/
  background-color: #eaeaea;
  margin: 0;
  box-sizing: border-box;
  line-height: 21px;
  padding: 8px 20px 8px 10px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  border-right: 1px solid #c6c6c6;
  line-height: 22px;
}
.c-grid-header ul.c-header-row > li.c-header-cell:hover {
  background-color: #f1f1f1;
}
.c-header-row .c-col-resizable {
  cursor: col-resize;
}
.c-grid-body {
  background-color: #fff;
  border-style: solid;
  border-color: #c6c6c6;
  border-width: 1px;
}
.c-grid-body .c-grid-viewport {
  width: 100%;
  position: relative;
  overflow: auto;
}
.c-grid-body .c-grid-viewport .c-grid-canvas {
  position: relative;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
li.c-column-locked {
  background-color: #f9f9f9;
}
